import { Component } from '@angular/core';
import { RouterModule } from '@angular/router';

@Component({
  selector: 'app-ias-createshare',
  standalone: true,
  imports: [RouterModule],
  templateUrl: './ias-createshare.component.html',
  styleUrl: './ias-createshare.component.scss'
})
export class IasCreateshareComponent {
  liked = false;
  count = 30;
  animate = false;

  toggleLike() {
    this.liked = !this.liked;
    this.count += this.liked ? 1 : -1;

    // Trigger animation
    this.animate = true;

    // Remove animation class after animation duration
    setTimeout(() => {
      this.animate = false;
    }, 300); // match the CSS animation duration
  }
}
