import { CommonModule } from '@angular/common';
import { Component } from '@angular/core';
import { RouterLink } from "@angular/router";

@Component({
  selector: 'app-ias-foundation',
  standalone: true,
  imports: [CommonModule, RouterLink],
  templateUrl: './ias-foundation.component.html',
  styleUrl: './ias-foundation.component.scss'
})
export class IasFoundationComponent {
faqs: any = [
    {
      title: 'Tenure Membership',
      status: '',
      description: 'Members posted in NCT of Delhi shall pay a tenure membership fee of ₹1000 per posting in Govt. of India.',
      isOpen: true
    },
    {
      title: 'Annual Membership',
      status: '',
      description: 'Other Members of the association posted in the NCR Delhi shall pay an annual membership free of Rs.200 .',
      isOpen: false
    },
    {
      title: 'Lifetime Membership ',
      status: ' (Retired Members)',
      description: 'Retired members shall pay a one-time life membership fee of Rs.500.',
      isOpen: false 
    }
  ];

  toggleFaq(index: number) {
    this.faqs = this.faqs.map((faq:any, i: number) => ({
      ...faq,
      isOpen: i === index ? !faq.isOpen : false // Only one open at a time
    }));
  }
}
