import { CommonModule, isPlatformBrowser } from '@angular/common';
import { Component, Inject, PLATFORM_ID, OnInit, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-ias-initiatives',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './ias-initiatives.component.html',
  styleUrls: ['./ias-initiatives.component.scss']
})
export class IasInitiativesComponent implements OnInit, OnDestroy {
  cards = [
    { title: 'IAS AGMUT Association', image: '../../../../assets/images/ias-agmut.png' },
    { title: 'IAS Association, AP', image: 'assets/images/ias-ap.png' },
    { title: 'Haryana IAS Association', image: 'assets/images/haryana.png' },
    { title: 'Karnataka IAS Association', image: 'assets/images/karnataka.png' },
    { title: 'Kerala IAS Association', image: 'assets/images/kerala.png' },
    { title: 'Odisha IAS Association', image: 'assets/images/odisha.png' },
    { title: 'Gujarat IAS Association', image: '../../../../assets/images/gujrat.png' },
    { title: 'Punjab IAS Association', image: 'assets/images/punjab.png' },
    { title: 'Rajasthan IAS Association', image: 'assets/images/rajasthan.png' },
    { title: 'Tamil Nadu IAS Association', image: 'assets/images/tamilnadu.png' },
    { title: 'Telangana IAS Association', image: 'assets/images/telangana.png' },
    { title: 'Bihar IAS Association', image: 'assets/images/bihar.png' },
    { title: 'UP IAS Association', image: 'assets/images/up.png' },
    { title: 'West Bengal IAS Association', image: 'assets/images/westbengal.png' }
  ];

  groupedCards: any[][] = [];
  currentIndex = 0;
  autoSlideInterval: any;
  isBrowser = false;

  constructor(@Inject(PLATFORM_ID) private platformId: Object) {
    this.groupCards();
  }

  ngOnInit(): void {
    this.isBrowser = isPlatformBrowser(this.platformId);

    if (this.isBrowser) {
      this.startAutoSlide();
    }
  }

  ngOnDestroy(): void {
    if (this.autoSlideInterval) {
      clearInterval(this.autoSlideInterval);
    }
  }

  groupCards() {
    this.groupedCards = []; // reset first
    for (let i = 0; i < this.cards.length; i += 3) {
      this.groupedCards.push(this.cards.slice(i, i + 3));
    }
  }

  nextSlide() {
    this.currentIndex = (this.currentIndex + 1) % this.groupedCards.length;
  }

  prevSlide() {
    this.currentIndex =
      (this.currentIndex - 1 + this.groupedCards.length) %
      this.groupedCards.length;
  }

  goToSlide(index: number) {
    this.currentIndex = index;
  }

  startAutoSlide() {
    this.autoSlideInterval = setInterval(() => {
      this.nextSlide();
    }, 5000); // every 5 seconds
  }
}
