import { Component, OnInit, Inject, PLATFORM_ID } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CommonModule, isPlatformBrowser } from '@angular/common';
import { environment } from '../../../../environments/environment';

@Component({
  selector: 'app-ias-monthlymeet',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './ias-monthlymeet.component.html',
  styleUrls: ['./ias-monthlymeet.component.scss'],
})
export class IasMonthlymeetComponent implements OnInit {
  blogs: any[] = [];

  constructor(private http: HttpClient, @Inject(PLATFORM_ID) private platformId: Object) {}

  ngOnInit(): void {
    // Only run HTTP request in browser
    if (isPlatformBrowser(this.platformId)) {
      this.loadLatestBlogs();
    }
  }

  loadLatestBlogs() {
    this.http
      .get<any>(`${environment.apiUrl}/events?page=1&limit=2&isActive=true`)
      .subscribe({
        next: (res) => {
          this.blogs = res.data.data; // latest 2 events
        },
        error: (err) => {
          console.error('Failed to load blogs', err);
        },
      });
  }
}
